































import Vue from 'vue';
import WorkspaceObjectSelector from '@/components/test_maker/WorkspaceObjectSelector.vue';
import { WorkspaceObjectModel } from '@/apis/models/WorkspaceObjectModel';

export default Vue.extend({
  components: {
    WorkspaceObjectSelector,
  },
  data() {
    return {
      defaultSelection: [] as WorkspaceObjectModel[],
      rootName: '',
      isQuizUnitOn: false,
      displaySelectAllToggle: false,
    };
  },
  computed: {
    quizLocalRootId(): string | null {
      if (this.$store.state.currentWorkspace) {
        return this.$store.state.currentWorkspace.quizFolders.local;
      }

      return null;
    },

    quizShareRootId(): string | null {
      if (this.$store.state.currentWorkspace) {
        return this.$store.state.currentWorkspace.quizFolders.share;
      }

      return null;
    },
  },
  methods: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    beforeOpen(event: any) {
      // import stuff that was selected before
      if (event.params.isQuizUnitOn) {
        this.isQuizUnitOn = event.params.isQuizUnitOn;
      }
      if (event.params.displaySelectAllToggle) {
        this.displaySelectAllToggle = event.params.displaySelectAllToggle;
      }
      if (event.params.defaultSelection && !this.isQuizUnitOn) {
        this.defaultSelection = event.params.defaultSelection;
      }
    },
    handleSubmit(selection: WorkspaceObjectModel[]) {
      this.$emit('submit-selection', selection);
      this.$modal.hide('select-quiz-group-modal');
    },
    handleCancel() {
      this.$modal.hide('select-quiz-group-modal');
    },
  },
});
